.recipe {
  border-radius: 10px;
  box-shadow: 0px 5px 20px grey;
  margin: 30px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: rgb(231, 230, 230);
  align-items: center;
  min-width: 35%;
  font-family: "Comic Sans MS", cursive, sans-serif;
  padding: 15px;
}

.image {
  border-radius: 50%;
  width: 100px;
  height: 100px;
}

.recipeLink {
  text-decoration: none;
  padding-bottom: 10px;
}

.recipeTitle {
  text-decoration: underline;
}
